import React from 'react'
import Link from 'next/link'
import { inject, observer } from 'mobx-react'

@inject('store')
@observer

class Mob extends React.Component {
  render() {
    return (
      <div>
        {store.movielist.map((movie, index) => <Card key={index} movie={movie}/> )}
      </div>
    )
  }
}

export default Mob;